@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box}body{margin:0}h1,h2,h3,p,ul{margin:0;padding:0}a{text-decoration:none;color:#000}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.app{font-family:'Poppins', sans-serif;font-weight:400}

.header{background-color:#F7F3F4}@media (min-width: 1024px){.header{padding-bottom:128px}}.header__content{margin-top:70px}@media (min-width: 1024px){.header__content{display:-webkit-flex;display:flex}}@media (min-width: 768px){.header__text-block{max-width:70%;margin:0 auto}}@media (min-width: 1024px){.header__text-block{max-width:45%;margin:0}}.header__title{color:transparent}.header__title-img{max-width:100%}@media (min-width: 1024px){.header__title-img{margin-bottom:59px}}.header__title-text{position:absolute;left:-100%}.header__description{font-size:15px;line-height:32px;margin-bottom:40px}.header__parrot-block{position:relative;padding-bottom:150px}@media (min-width: 1024px){.header__parrot-block{margin-top:30px}}@media (min-width: 1439px){.header__parrot-block{margin-top:85px}}.header__parrot{position:absolute;right:-250px}@media (min-width: 768px){.header__parrot{right:205px}}@media (min-width: 1024px){.header__parrot{right:-55%;width:100%}}.header__card{margin-left:120px;padding:33px;width:215px;height:255px;background-image:url(../../static/media/CardBG.5bdd08dc.svg);background-position:center;border-radius:30px;-webkit-transform:translateY(85%);transform:translateY(85%)}.header__card-icon{width:33px}.header__card-text{margin-top:90px;color:#fff;font-weight:700}.header__card-members{font-size:15px;line-height:140%}.header__card-count{font-size:40px;line-height:100%}

.menu{height:70vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;text-align:center}@media (min-width: 667px){.menu{-webkit-flex-direction:row;flex-direction:row;height:-webkit-min-content;height:min-content}}.menu__list{list-style:none}@media (min-width: 667px){.menu__list{display:-webkit-flex;display:flex}}.menu__item{margin-top:30px;padding:13px}@media (min-width: 667px){.menu__item{margin-top:0;width:-webkit-max-content;width:max-content}}@media (min-width: 1200px){.menu__item{padding:0 23px;margin:0 28px}}.menu__link{font-size:28px;opacity:0.9;position:relative}.menu__link--home{font-weight:700}@media (min-width: 667px){.menu__link{font-size:16px}}.menu__link:before{content:"";position:absolute;z-index:-1;left:0;right:100%;bottom:0;background:#FFBD9E;height:2px;transition-property:left, right;transition-duration:0.4s;transition-timing-function:ease-out}.menu__link:hover:before,.menu__link:focus:before,.menu__link:active:before{right:0}.menu__btn{height:48px;width:100%;background-color:#fff;box-shadow:0 30px 40px rgba(240,101,126,0.05);border-radius:40px;border-color:transparent;cursor:pointer;transition:background-color 0.4s, color 0.4s;font-weight:700;font-size:16px;line-height:140%}@media (min-width: 667px){.menu__btn{width:145px;margin-left:50px}}@media (min-width: 1200px){.menu__btn{margin-left:200px}}.menu__btn:hover{background-color:#FFBD9E;color:#fff}

.nav__logo{cursor:pointer}.nav__wrapper{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;padding:27px 0}.nav__menu-desktop{display:none}@media (min-width: 1024px){.nav__menu-desktop{display:block}}.nav__burger{background-color:transparent;border:none;outline:none}@media (min-width: 1024px){.nav__burger{display:none}}.nav__burger--active :first-child{-webkit-transform:rotate(45deg) translateY(2px);transform:rotate(45deg) translateY(2px)}.nav__burger--active :nth-child(2){display:none}.nav__burger--active :last-child{-webkit-transform:rotate(-45deg) translateY(-2px);transform:rotate(-45deg) translateY(-2px)}.nav__burger-item{height:3px;width:22px;background-color:#000;transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s}.nav__burger-item--short{margin:4px 0 4px 4px;width:17px}.divider{height:3px;background-color:#222433}.slide-enter{max-height:0;overflow:hidden}.slide-enter-active{max-height:58px;transition:max-height 300ms ease-in-out}.slide-exit{max-height:58px;overflow:hidden}.slide-exit-active{max-height:0;transition:max-height 300ms ease-in-out}

.container{padding:0 18px;max-width:1440px;margin:0 auto}@media (min-width: 667px){.container{padding:0 30px}}@media (min-width: 1200px){.container{padding:0 80px}}

.searchInput{padding:8px;width:100%;height:64px;background:#fff;box-shadow:0 20px 20px rgba(240,101,126,0.05);border-radius:40px;position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.searchInput__icon{position:absolute;margin-left:17px}.searchInput__input{width:100%;padding-left:59px;border-color:transparent;outline:none}.searchInput__input::-webkit-input-placeholder{font-weight:400;font-size:14px;line-height:24px;color:#807C7C}.searchInput__input:-ms-input-placeholder{font-weight:400;font-size:14px;line-height:24px;color:#807C7C}.searchInput__input::placeholder{font-weight:400;font-size:14px;line-height:24px;color:#807C7C}

.button{height:48px;color:#fff;border-color:transparent;background-color:#F2726C;border-radius:30px;font-weight:700;font-size:12px;line-height:140%;letter-spacing:1px;text-transform:uppercase}

